.header {
  margin-top: 60px;
}
.headerTitles {
  display: flex;
  flex-direction: column;
  flex: 100%;
  align-items: center;
  font-family: "Lora", serif;
  color: #444;
}

.headerTitleSm {
  padding-top: 50px;
  top: 18%;
  font-size: 20px;
}

.headerTitleLg {
  padding-top: 55px;
  top: 20%;
  font-size: 100px;
}

.headerImg {
  width: 75%;
  height: 425px;
  margin-top: 80px;
  object-fit: cover;
}

